import React, { Component } from "react";
import { connect } from "react-redux";
import { Table, Menu, Dropdown, Button, Form, Alert } from "antd";
import { DownOutlined } from "@ant-design/icons";
import LayoutPage from "./LayoutPage";
import {
  requestAllDatasets,
  requestAddNewDataset,
  requestDeleteDataset,
} from "../actions";

class DatasetListPage extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    this.props.fetchDatasets();
  }

  /**
   * - id
- name
- tags
- description
- createdAt/ lastModified
- source:  
   + GENERATED
   + MUTATED
   + RECORDED

   * @param {Object} ds The selected dataset to be duplicated
   */
  duplicateDataset(ds) {
    const currentTime = Date.now();
    const newDS = {
      id: `${ds.id}-[duplicated]`,
      name: `${ds.name}-[duplicated]`,
      description: ds.description,
      tags: ds.tags,
      createdAt: currentTime,
      lastModified: currentTime,
      source: 'MUTATED',
    };
    this.props.addNewDataset(newDS);
  }

  render() {
    const { datasets, deleteDataset } = this.props;
    const dataSource = datasets.map((ds) => ({ ...ds, key: ds.id }));
    const columns = [
      {
        title: "Id",
        key: "data",
        render: (ds) => <a href={`/data-sets/${ds.id}`}> {ds.id} </a>,
      },
      {
        title: "Action",
        key: "data",
        render: (ds) => (
          <Dropdown
            overlay={
              <Menu>
                <Menu.Item
                  key="simulate"
                >
                  <a href={`/simulation?datasetId=${ds.id}`}>Simulate</a>
                </Menu.Item>
                <Menu.Item
                  key="duplicate"
                  onClick={() => this.duplicateDataset(ds)}
                >
                  Duplicate
                </Menu.Item>
                <Menu.Item
                  key="delete"
                  onClick={() => deleteDataset(ds.id)}
                >
                  Delete
                </Menu.Item>
              </Menu>
            }
          >
            <a
              className="ant-dropdown-link"
              onClick={(e) => e.preventDefault()}
            >
              <Button>
                Action <DownOutlined />
              </Button>
            </a>
          </Dropdown>
        ),
      },
    ];
    return (
      <LayoutPage
        pageTitle="Dataset"
        pageSubTitle="All the datasets"
      > 
        <a href={`/data-sets/new-dataset-${Date.now()}`}>
          <Button style={{ marginBottom: "10px" }}>Add New Dataset</Button>
        </a>
        <Table columns={columns} dataSource={dataSource} />
      </LayoutPage>
    );
  }
}

const mapPropsToStates = ({ datasets }) => ({
  datasets: datasets.allDatasets,
});

const mapDispatchToProps = (dispatch) => ({
  fetchDatasets: () => dispatch(requestAllDatasets()),
  deleteDataset: (datasetId) =>
    dispatch(requestDeleteDataset(datasetId)),
  addNewDataset: (dataset) =>
    dispatch(requestAddNewDataset(dataset)),
});

export default connect(
  mapPropsToStates,
  mapDispatchToProps
)(DatasetListPage);
