import React, { Component } from "react";
import { connect } from "react-redux";
import { List, Button, Menu, Dropdown } from "antd";
import {
  ClearOutlined,
  ImportOutlined,
  DownOutlined,
  CaretRightOutlined,
  CopyOutlined,
  DeleteOutlined,
} from "@ant-design/icons";
import LayoutPage from "./LayoutPage";
import {
  requestAllModels,
  requestDeleteModel,
  requestDuplicateModel,
  requestAddNewModel,
} from "../actions";

class ModelListPage extends Component {
  constructor(props) {
    super(props);
  }

  onUpload(files) {
    const fileReader = new FileReader();
    fileReader.onloadend = () => {
      try {
        const newModel = JSON.parse(fileReader.result);
        this.props.importNewModel(newModel);
      } catch (error) {
        this.props.setNotification({ type: "error", message: error });
      }
    };
    fileReader.readAsText(files[0]);
  }

  componentDidMount() {
    this.props.fetchAllModels();
  }

  render() {
    const { allModels, deleteModel, duplicateModel } = this.props;
    return (
      <LayoutPage
        pageTitle="Model"
        pageSubTitle="Model defines the topology and the specification of the sensors, actuators and the gateways"
      >
        <Dropdown
          overlay={
            <Menu>
              <Menu.Item key="model:3">
                <a href={`/models/new-model-${Date.now()}`}>
                  <ClearOutlined /> Create New
                </a>
              </Menu.Item>
              <Menu.Item
                key="model:1"
                onClick={() => this.inputFileDOM.click()}
              >
                <ImportOutlined /> Import From File
                <input
                  type="file"
                  onChange={(event) => this.onUpload(event.target.files)}
                  ref={(input) => {
                    this.inputFileDOM = input;
                  }}
                  style={{ display: "none" }}
                  accept=".json"
                  multiple={false}
                />
              </Menu.Item>
            </Menu>
          }
          trigger={["click"]}
        >
          <Button
            className="ant-dropdown-link"
            onClick={(e) => e.preventDefault()}
            style={{ marginBottom: "15px" }}
          >
            Add Model <DownOutlined />
          </Button>
        </Dropdown>
        <List
          bordered
          dataSource={allModels}
          renderItem={(item) => (
            <List.Item
              actions={[
                <a
                  key="item-edit"
                  type="button"
                  href={`/simulation?model=${item}`}
                >
                  <Button>
                    <CaretRightOutlined /> Simulate
                  </Button>
                </a>,
                <Button key="item-delete" onClick={() => duplicateModel(item)}>
                  <CopyOutlined /> Duplicate
                </Button>,
                <Button
                  key="item-delete"
                  onClick={() => deleteModel(item)}
                  type="danger"
                >
                  <DeleteOutlined />
                  Delete
                </Button>,
              ]}
            >
              <a href={`/models/${item}`}>{item}</a>
            </List.Item>
          )}
        />
      </LayoutPage>
    );
  }
}

const mapPropsToStates = ({ allModels }) => ({
  allModels,
});

const mapDispatchToProps = (dispatch) => ({
  fetchAllModels: () => dispatch(requestAllModels()),
  deleteModel: (modelFileName) => dispatch(requestDeleteModel(modelFileName)),
  duplicateModel: (modelFileName) =>
    dispatch(requestDuplicateModel(modelFileName)),
  importNewModel: (model) => dispatch(requestAddNewModel(model)),
});

export default connect(mapPropsToStates, mapDispatchToProps)(ModelListPage);
